import { memo, useEffect, useState } from 'react';
import { useFormikContext } from 'formik';
import { Grid, FormHelperText, InputLabel } from '@material-ui/core';
import { DatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import { MaterialUiPickersDate } from '@material-ui/pickers/typings/date';
import { TEditDoc } from '../types';
import { useSelectStyles } from './styles';
import { TERMINATION_DATE_FORMAT } from '../../../config';
import { doesNeedPAUpload } from '../helpers';

const DocumentDate = () => {
	const classes = useSelectStyles();
	const { values, touched, errors, setFieldValue } =
		useFormikContext<TEditDoc>();
	const [isShow, setIsShow] = useState(false);

	useEffect(() => {
		setIsShow(doesNeedPAUpload(values.docType));
	}, [values.docType.id]);

	const errorText = touched.paUploadedDate && errors.paUploadedDate;
	const hasError = Boolean(errorText);

	if (!isShow) return <></>;

	return (
		<Grid item xs={12}>
			<InputLabel
				htmlFor='document-paUploadedDate-label'
				className={classes.label}
				error={hasError}
			>
				Date of Purchase Agreement Upload
			</InputLabel>
			<MuiPickersUtilsProvider utils={DateFnsUtils}>
				<DatePicker
					disablePast
					format={TERMINATION_DATE_FORMAT}
					value={values.paUploadedDate ? new Date(values.paUploadedDate) : null}
					fullWidth
					placeholder='Date of Purchase Agreement Upload'
					onChange={(date: MaterialUiPickersDate) =>
						setFieldValue('paUploadedDate', date)
					}
					name='paUploadedDate'
				/>
			</MuiPickersUtilsProvider>

			<FormHelperText id='document-paUploadedDate-label' error={hasError}>
				{errorText}
			</FormHelperText>
		</Grid>
	);
};

export default memo(DocumentDate);
